import React from 'react';
import logo from './idm4.png';
import techlogo from '../techstyl/images/version/tech-logo.png';

const Navbar = (props) => (
        <header className="tech-header header">

		{/*<!--Navigation bar-->*/}
		<script src="https://idm4.tk/jsengn/jquery.min.js"></script>
		<script src="https://idm4.tk/jsengn/bootstrap.min.js"></script>

		<div id="nav-placeholder" className="container-fluid">
			<nav className="navbar navbar-toggleable-md navbar-inverse fixed-top bg-inverse">
						<button className="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="#navbarCollapse" aria-controls="navbarCollapse" aria-expanded="false" aria-label="Toggle navigation">
							<span className="navbar-toggler-icon"></span>
						</button>
						<a className="navbar-brand" href="./techstyl/tech-index.html"> <img src={techlogo} alt="logo"/></a>
						<div className="collapse navbar-collapse" id="navbarCollapse">
							<ul className="navbar-nav mr-auto">
								<li className="nav-item">
									<a className="nav-link" href="./techstyl/tech-index.html">{props.mb1}</a>
								</li>
								<li className="nav-item dropdown has-submenu menu-large hidden-md-down hidden-sm-down hidden-xs-down">
									<a className="nav-link dropdown-toggle" href="./techstyl/#" id="dropdown01" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">News</a>
									<ul className="dropdown-menu megamenu" aria-labelledby="dropdown01">
										<li>
											<div className="container">
												<div className="mega-menu-content clearfix">
													<div className="tab">
														<button className="tablinks active" onclick="openCategory(event, 'cat01')">Science</button>
														<button className="tablinks" onclick="openCategory(event, 'cat02')">Technology</button>
														<button className="tablinks" onclick="openCategory(event, 'cat03')">Social Media</button>
														<button className="tablinks" onclick="openCategory(event, 'cat04')">Car News</button>
														<button className="tablinks" onclick="openCategory(event, 'cat05')">Worldwide</button>
													</div>

													<div className="tab-details clearfix">
														<div id="cat01" className="tabcontent active">
															<div className="row">
																<div className="col-lg-3 col-md-6 col-sm-12 col-xs-12">
																	<div className="blog-box">
																		<div className="post-media">
																			<a href="./techstyl/tech-single.html" title="">
																				<img src="./techstyl/upload/tech_menu_01.jpg" alt="" className="img-fluid"/>
																				<div className="hovereffect">
																				</div>
																				<span className="menucat">Science</span>
																			</a>
																		</div>{/* <!-- end media --> */}
																		<div className="blog-meta">
																			<h4><a href="./techstyl/tech-single.html" title="">Top 10+ care advice for your toenails</a></h4>
																		</div>{/* <!-- end meta --> */}
																	</div>{/* <!-- end blog-box --> */}
																</div>

																<div className="col-lg-3 col-md-6 col-sm-12 col-xs-12">
																	<div className="blog-box">
																		<div className="post-media">
																			<a href="./techstyl/tech-single.html" title="">
																				<img src="./techstyl/upload/tech_menu_02.jpg" alt="" className="img-fluid"/>
																				<div className="hovereffect">
																				</div> {/* <!-- end hover --> */}
																				<span className="menucat">Science</span>
																			</a>
																		</div> {/* <!-- end media --> */}
																		<div className="blog-meta">
																			<h4><a href="./techstyl/tech-single.html" title="">The secret of your beauty is handmade soap</a></h4>
																		</div>{/* <!-- end meta --> */}
																	</div>{/* <!-- end blog-box --> */}
																</div>
															</div>{/* <!-- end row --> */}
														</div>
													</div>{/* <!-- end tab-details --> */}
												</div>{/* <!-- end mega-menu-content --> */}
											</div>
										</li>
									</ul>
								</li>
								<li className="nav-item">
									<a className="nav-link" href="./techstyl/tech-category-01.html">Gadgets</a>
								</li>                   
								<li className="nav-item">
									<a className="nav-link" href="./techstyl/tech-category-02.html">Videos</a>
								</li>
								<li className="nav-item">
									<a className="nav-link" href="./techstyl/tech-category-03.html">Reviews</a>
								</li>
								<li className="nav-item">
									<a className="nav-link" href="./techstyl/tech-contact.html">Contact Us</a>
								</li>
							</ul>
							<ul className="navbar-nav mr-2">
								<li className="nav-item">
									<a className="nav-link" href="./techstyl/#"><i className="fa fa-rss"></i></a>
								</li>
								<li className="nav-item">
									<a className="nav-link" href="./techstyl/#"><i className="fa fa-android"></i></a>
								</li>
								<li className="nav-item">
									<a className="nav-link" href="./techstyl/#"><i className="fa fa-apple"></i></a>
								</li>
							</ul>
						</div>
					</nav>
		</div> 
        </header> 
		
);

export default Navbar;